import { NextRequest, NextResponse } from 'next/server';
import connectDB from '@/lib/db';
import Product from '@/lib/models/Product';
import Category from '@/lib/models/Category';
import { productSchema, categorySchema } from '@/lib/validations';
import { adminMiddleware } from '@/lib/middleware';
import { successResponse, errorResponse } from '@/lib/api-response';

// Create product (admin only)
export async function POST(request: NextRequest) {
    const authError = await adminMiddleware(request);
    if (authError) return authError;

    try {
        await connectDB();

        const body = await request.json();

        const validated = productSchema.safeParse(body);
        if (!validated.success) {
            const { response, status } = errorResponse(validated.error.issues[0].message);
            return NextResponse.json(response, { status });
        }

        const product = await Product.create(validated.data);

        return NextResponse.json(
            successResponse(product, 'Product created successfully'),
            { status: 201 }
        );
    } catch (error: any) {
        console.error('Create product error:', error);
        if (error.code === 11000) {
            const { response, status } = errorResponse('Product with this slug already exists');
            return NextResponse.json(response, { status });
        }
        const { response, status } = errorResponse('Failed to create product');
        return NextResponse.json(response, { status });
    }
}

// Get all products (admin)
export async function GET(request: NextRequest) {
    const authError = await adminMiddleware(request);
    if (authError) return authError;

    try {
        await connectDB();

        const { searchParams } = new URL(request.url);
        const page = parseInt(searchParams.get('page') || '1');
        const limit = parseInt(searchParams.get('limit') || '20');

        const skip = (page - 1) * limit;
        const products = await Product.find()
            .populate('category', 'name slug')
            .sort('-createdAt')
            .skip(skip)
            .limit(limit)
            .lean();

        const total = await Product.countDocuments();

        return NextResponse.json(
            successResponse({
                products,
                pagination: {
                    page,
                    limit,
                    total,
                    pages: Math.ceil(total / limit),
                },
            })
        );
    } catch (error: any) {
        console.error('Get products error:', error);
        const { response, status } = errorResponse('Failed to fetch products');
        return NextResponse.json(response, { status });
    }
}
